import React, { ReactNode } from "react";
import styled from "styled-components";

import LinkButton from "../Button/LinkButton";
import { ButtonGroup, ButtonGroupButtonProps } from "../ButtonGroup";
import { colors, spacingMap } from "../styleguide";
import Typography from "../Typography/Typography";
import { pxToRem } from "../utils";

import { HEAD_ROW_HEIGHT } from "./DataTable.styles";

export interface TableActionsBarProps {
	selectedCountMessage?: string;
	/**
	 * This is handled mainly in the DataTable but can be passed as a callback
	 */
	handleDeselect?: () => void;
	tableBulkActions: ButtonGroupButtonProps[] | ReactNode;
}

const TableActionsBarContainer = styled.div`
	display: flex;
	flex-direction: row;
	flex: 1;
	align-items: center;
	justify-content: space-between;
	position: absolute;
	left: 4rem;
	height: ${HEAD_ROW_HEIGHT};
	right: ${spacingMap.sm};
	margin: auto;
	background: ${colors.lightestGray};
	border-bottom: ${`1px solid ${colors.lightGray}`};
	box-sizing: border-box;
`;

const LeftContent = styled.div`
	display: inline-flex;
	align-items: center;
	gap: ${pxToRem(4)};
`;

export const TableActionsBar = ({
	selectedCountMessage,
	handleDeselect,
	tableBulkActions,
}: TableActionsBarProps) => (
	<TableActionsBarContainer data-qa="table-actions-bar" aria-live="polite">
		<LeftContent>
			<Typography
				variant="bodyCopy"
				dataQA="selected-count-message"
			>
				{selectedCountMessage}
			</Typography>
			<LinkButton
				dataQA="deselect-selected-rows"
				buttonText="Deselect"
				onClick={handleDeselect}
			/>
		</LeftContent>
		<div>
			{Array.isArray(tableBulkActions) ? (
				<ButtonGroup
					dataQA="icon-button-group"
					ariaLabel="Button Group"
					buttons={tableBulkActions}
					buttonSize="small"
				/>
			) : (
				tableBulkActions
			)}
		</div>
	</TableActionsBarContainer>
);
